<template>
    <div class="modal fade" id="modal-delete">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true" class="fa fa-remove"></span></button>
                    <h5 class="modal-title">Confirmar</h5>
                </div>
                <div class="modal-body">
                    <h4>Desea eliminar el registro?</h4>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default pull-left" data-dismiss="modal">Cancelar</button>
                    <button @click="onDelete" data-dismiss="modal" type="button" class="btn btn-primary">Eliminar</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "c-delete",
        props:{
          model:{
              type:Object,
              default:null,
              required:false
          }
        },
        methods:{
            onDelete(){
                this.$emit('onDelete',this.model);
            }
        },
    }
</script>